@import url(https://fonts.googleapis.com/css2?family=Jost:wght@300;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Inconsolata:wght@500&display=swap);
body {
    margin: 0;
}

h1,
p,
div,
button {
    font-family: 'Jost', sans-serif;
}

* {
	transition: color 0.15s, background-color 0.15s;
}

header {
    width: 100%;
    height: 65px;
}

.wrapper {
    max-width: 970px;
    margin: 0 auto;
    height: 100%;
}

header .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.menu-wrapper {
    width: 450px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
}

.menu-button {
    border: none;
    border-radius: 25px;
    padding: 8px 22px;
    font-size: 1rem;
    transition: 0.5s ease;
}

.menu-button:hover {
    cursor: pointer;
}

.menu-item,
li {
    font-size: 1rem;
    transition: 0.5s ease;
    width: 100%;
    height: 100%;
    text-decoration: none;
}

.menu-link,
.menu-item:last-child {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;
}

.menu-item:hover,
li:hover {
    cursor: pointer;
}

@media (max-width: 1224px) {
    .wrapper {
        max-width: 720px;
        padding: 0 1em;
    }
}

@media (max-width: 768px) {
    .wrapper {
        max-width: 640px;
    }
}

@media (max-width: 680px) {
    .wrapper {
        max-width: 540px;
    }
    .menu-wrapper {
        width: 350px;
    }
}

@media (max-width: 540px) {
    .menu-item:nth-last-child(2),
    .menu-item:nth-last-child(3) {
        display: none;
    }

    .menu-button {
        padding: 6px 16px;
    }
}

.toggle-wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-bottom: 15px;
}

.toggle-wrapper:last-child {
	margin-bottom: 0;
}

.toggle-wrapper > p {
	margin: 0 0 5px 0;
	font-size: 15px;
	text-align: center;
	line-height: 15px;
}

.toggle-wrapper > .swatch {
	width: 100px;
	margin: 0 0 5px 0;
	text-align: center;
	padding: 4px;
	border-radius: 5px;
}

.toggle-navigation {
	display: flex;
	align-items: center;
	justify-content: space-between;
	width: 100px;
}

.toggle-navigation > button {
	background-color: #fff;
	border: none;
}

.toggle-navigation > button:first-of-type {
	border-radius: 10px 0 0 10px;
}
.toggle-navigation > button:last-of-type {
	border-radius: 0 10px 10px 0;
}

.toggle-navigation > div {
}

footer {
    margin-top: 3rem;
    min-height: 100px;
    overflow: auto;
}

footer .wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin: 1rem auto;
}

.footer-item {
    width: 185px;
    /* text-align: center; */
}

.footer-item h5 {
    text-align: center;
    font-size: 0.9rem;
}

.footer-item p {
    font-size: 0.8rem;
}

.footer-item ul li {
    list-style: none;
    font-size: 0.8rem;
    transition: 0.5s ease;
}

.footer-item ul li:hover {
    cursor: pointer;
}

.footer-item p,
.footer-item ul,
.social-container {
    padding-left: 0;
    margin-top: 12px;
    padding-top: 0;
}

.social-container {
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.social-img {
    width: 25px;
    height: auto;
    transition: 0.5s ease;
}

.social-img:hover {
    cursor: pointer;
}

.footer-btn {
    border-radius: 15px;
    padding: 6px 18px;
    font-size: 0.7rem;
    font-weight: 700;
    transition: 0.5s ease;
    min-width: 95px;
    width: 50%;
}

.footer-btn:hover {
    cursor: pointer;
}

@media (max-width: 1224px) {
    .footer-item {
        width: 165px;
    }
    .footer-item h5 {
        font-size: 0.9rem;
        min-height: 40px;
        margin: 0;
        padding: 1rem 0;
    }
}

@media (max-width: 768px) {
    .footer-item {
        width: 140px;
    }
    .footer-item p {
        font-size: 0.7rem;
    }
}

@media (max-width: 680px) {
    .social-img {
        width: 20px;
    }
    .footer-item {
        width: 120px;
    }
}

@media (max-width: 540px) {
    footer .wrapper {
        display: grid;
        /* flex-direction: column; */
        /* align-items: center; */
        grid-template-rows: auto;
        grid-template-columns: 1fr 1fr;
        grid-template-areas:
            'about links'
            'action social';
    }
    .footer-item:nth-child(1) {
        grid-area: about;
    }
    .footer-item:nth-child(2) {
        grid-area: links;
    }
    .footer-item:nth-child(3) {
        grid-area: action;
    }
    .footer-item:nth-child(4) {
        grid-area: social;
    }

    .footer-item {
        margin: 0 auto;
        width: 80%;
    }
}

.item {
    position: relative;
    width: 250px;
    padding: 20px;
    border-radius: 10px;
    margin-top: 3rem;
    text-align: center;
    padding-bottom: 80px;
}

.item img {
    border-radius: 5px;
}

.img-container {
    min-height: 140px;
}

.item button {
    position: absolute;
    bottom: 30px;
    border: none;
    border-radius: 25px;
    padding: 8px 22px;
    font-size: 1rem;
    transition: 0.5s ease;
    left: 50%;
    transform: translateX(-50%);
}

.item button:hover {
    cursor: pointer;
}

.item p {
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 5rem;
}

.item h3 {
    min-height: 2rem;
}

@media (max-width: 680px) {
    .item {
        width: 200px;
        margin-top: 1em;
        padding: 16px 16px 80px;
    }
    .item p {
        min-height: 3em;
    }
}

@media (max-width: 540px) {
    .item {
        padding: 20px;
        width: 100%;
        display: grid;
        grid-gap: 5px;
        gap: 5px;
        grid-template-rows: auto 1fr;
        grid-template-columns: 1fr 1fr;
        grid-template-areas:
            'img info'
            'button button';
    }

    .img-container {
        grid-area: img;
    }

    .item-info {
        grid-area: info;
    }

    .item-button-wrapper {
        padding-top: 20px;
        grid-area: button;
    }

    .item button {
        position: static;
        position: initial;
        left: auto;
        left: initial;
        bottom: auto;
        bottom: initial;
        transform: none;
        transform: initial;
    }
}

@media (max-width: 450px) {
    .item {
        width: 100%;
        max-width: 250px;
        grid-template-rows: auto auto 1fr;
        grid-template-columns: auto;
        grid-template-areas:
            'img'
            'info'
            'button';
    }
}

.main {
    display: grid;
    grid-template-columns: 3fr 2fr;
    grid-template-rows: 1fr auto;
    grid-template-areas:
        'text img'
        'items items';
    padding-top: 2rem;
    grid-gap: 2vh;
}

.main-background-image {
    grid-area: img;
    background-image: url(../../static/media/illustration.0604dbbb.svg);
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
}

.main-btn {
    border-radius: 15px;
    padding: 8px 22px;
    font-size: 1rem;
    font-weight: 700;
    color: white;
    transition: 0.5s ease;
}

.main-btn:hover {
    cursor: pointer;
}

.main-text h1 {
    grid-area: text;
    margin-top: 0;
}

.wrapper-items {
    margin-top: 3rem;
    text-align: center;
    grid-area: items;
}

.items {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

#body-decoy {
    position: fixed;
    width: 1px;
    height: 1px;
    top: 50%;
    left: 50%;
}

@media (max-width: 1224px) {
    .main {
        grid-template-columns: 3fr 1fr;
        grid-template-areas:
            'text img'
            'items items';
        padding-top: 1rem;
        grid-gap: 1em;
    }

    .main-background-image {
        min-height: 230px;
        width: auto;
    }
}

@media (max-width: 680px) {
    .main {
        grid-template-columns: 3fr;

        grid-template-areas:
            'img'
            'text'
            'items';
    }
    .wrapper-items {
        margin-top: 1em;
    }
    .main-text p {
        font-size: 1em;
    }
}

@media (max-width: 540px) {
    .items {
        justify-content: center;
    }
}

.switcher {
    z-index: 75;
    display: flex;
    flex-direction: row;
    position: fixed;
    align-items: center;
    right: -316px;
    top: 50%;
    transform: translateY(-50%);
    transition: 0.6s ease-in-out;
    min-width: 2px;
    min-height: 2px;
}

.big,
.small {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: 0.6s ease-in-out;
    border: 1px solid white;
    color: white;
}

.small {
    position: absolute;
    right: 300px;
    z-index: 5;
    cursor: pointer;
    height: 40px;
    width: 55px;
    /* background-color: #ddd; */
    border-radius: 10px;
    font-weight: 700;
    font-size: 20px;
    border-right: none;
    border-bottom-right-radius: 2px;
    border-top-right-radius: 2px;
}

span {
    display: block;
    transition: 0.6s ease-in-out;
    transform: translateX(-10px);
}

.small span {
    width: 20px;
}

.rotate {
    transform: rotate(-180deg) translateX(20px);
}

.big {
    position: absolute;
    right: 15px;
    height: 500px;
    width: 300px;
    /* background-color: #ddd; */
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
}

.opened {
    transform: translateX(-300px);
}

.palette-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.nav-wrapper,
.controls-wrapper {
    margin: 16px 0;
    display: flex;
    justify-content: space-between;
    width: 200px;
}

.nav-btn,
.controls-btn {
    width: 80px;
    border: 1px solid white;
    border-radius: 15px;
}

.nav-btn {
    background-color: black;
    color: white;
}

.edit {
    background-color: red;
    color: white;
}

.new {
    background-color: blue;
    color: white;
}

.index {
    text-align: center;
    margin: 8px 0;
}

.nav-wrapper {
    margin-bottom: 0;
}

.help-button {
    border: none;
    position: absolute;
    left: 15px;
    bottom: 15px;
    width: 25px;
    height: 25px;
    background-color: white;
    color: #333;
    border-radius: 25px;
}

.swatches-wrapper {
	font-size: 14px;
	font-weight: 300;
	width: 200px;
	min-height: 160px;
	background-color: black;
	color: white;
	text-align: center;
	display: flex;
	flex-wrap: wrap;
}

.swatch {
	font-family: 'Inconsolata', monospace;
	line-height: 30px;
	width: 50%;
	font-weight: 500;
	padding: 4px;
	box-sizing: border-box;
	cursor: default;
	text-align: left;
	padding-right: 16px;
	height: 35px;
	text-align: right;
}

.swatches-wrapper p {
	display: block;
	width: 100%;
	margin: 0;
	padding: 4px;
	border-radius: 10px;
}

.modal {
	z-index: 10;
	position: fixed;
	top: 0;
	left: 0;
	width: 100vw;
	height: 100vh;
	background: rgba(0, 0, 0, 0.3);
}

.modal-closed {
	display: none;
}

.modal-edit {
	box-sizing: border-box;
	padding: 10px;
	z-index: 11;
	width: 400px;
	min-height: 500px;
	background-color: #bbb;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translateX(-50%) translateY(-50%);
	box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.3);
	text-align: center;
	border-radius: 15px;
}

.modal-edit p {
	display: inline-block;
	margin-top: 0;
}

.modal-edit p + input {
	margin-left: 10px;
}

.modal-swatch-wrapper {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-around;
}

.modal-btn-wrapper {
	display: flex;
	justify-content: space-around;
}

.modal-btn {
	margin: 16px 0;
	width: 80px;
	border: 1px solid white;
	border-radius: 15px;
	color: white;
}

.save {
	background-color: green;
}

.delete {
	background-color: red;
}

.import {
	background-color: blue;
}

.swatch-edit-wrapper {
	margin: 5px;
	width: 160px;
	height: 55px;
	display: grid;
	grid-template-rows: 1fr 1fr;
	grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
	grid-template-areas:
		'num dis dis dis dis'
		'num inp inp inp btn';
	grid-gap: 5px;
}

.swatch-edit-number {
	display: flex;
	justify-content: center;
	align-items: center;
	grid-area: num;
}

.swatch-edit-display {
	grid-area: dis;
}

.swatch-edit-input {
	grid-area: inp;
	border: none;
}

.swatch-edit-set-btn {
	grid-area: btn;
	border: none;
	border-radius: 5px;
}

.modal-edit-close-btn {
	position: absolute;
	right: 20px;
	top: 20px;
	color: black;
	font-size: 20px;
	line-height: 12px;
	cursor: pointer;
}

.modal-2 {
	z-index: 12;
}

.import-textarea {
	width: 90%;
	min-height: 64px;
	resize: none;
}

.modal-import {
	box-sizing: border-box;
	padding: 15px;
	z-index: 13;
	min-width: 350px;
	background-color: #bbb;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translateX(-50%) translateY(-50%);
	box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.3);
	text-align: center;
	border-radius: 15px;
}

.type-btn {
	margin: 16px 0;
	width: 80px;
	border: 1px solid white;
	border-radius: 15px;
	color: white;
	background-color: #333;
}

.type-btn:hover {
	background-color: #333366;
}

.type-btn-active {
	background-color: blue;
}
.type-btn-active:hover {
	background-color: blue;
}

.modal-3 {
	z-index: 100;
}

.modal-error-window {
	box-sizing: border-box;
	padding: 15px;
	z-index: 100;
	min-width: 350px;
	background-color: #bbb;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translateX(-50%) translateY(-50%);
	box-shadow: 10px 10px 10px rgba(0, 0, 0, 0.3);
	text-align: center;
	border-radius: 15px;
}

.modal-error {
	color: red;
}

.modal-error-btn {
	margin: 16px 0;
	width: 80px;
	border: 1px solid white;
	border-radius: 15px;
	color: white;
	background-color: green;
}

.help-overlay {
	z-index: 1000;
	position: fixed;
	width: 100vw;
	height: 100vh;
	background-color: rgba(0, 0, 0, 0.9);
	mix-blend-mode: multiply;
	cursor: pointer;
}

#help-spotlight {
	/* left: 250px; */
	/* top: 100px; */
	background-color: white;
	position: absolute;
	width: 100px;
	height: 100px;
	border-radius: 100px;
	border: solid 2px red;
	transform: translateX(-50%) translateY(-50%);
	transform-origin: center;
	transition: all ease-in-out 2s;
}

.help-title {
	margin: 0;
}

.help-title-bg {
	min-width: 250px;
	border-radius: 15px;
	padding: 1em;
	background-color: rgba(0, 0, 0, 0.9);
	z-index: 1001;
	position: fixed;
	color: white;
	text-align: center;
	transform: translateX(-50%) translatey(-50%);
	left: 50%;
	top: 50%;
	transition: all ease-in-out 0.6s;
}

.no-scroll {
	height: 100vh;
	overflow: hidden;
}

